import { NextResponse } from "next/server";
import { amadeus } from "@/lib/amadeus";

export async function GET(req: Request) {
  try {
    const { searchParams } = new URL(req.url);
    const latitude = searchParams.get("lat");
    const longitude = searchParams.get("lon") || searchParams.get("lng");
    const radius = Number(searchParams.get("radius") || 5);
    const pageLimit = Number(searchParams.get("limit") || 20);

    if (!latitude || !longitude) {
      return NextResponse.json(
        { error: "Missing coordinates" },
        { status: 400 }
      );
    }

    const response = await amadeus.get(
      "v1/reference-data/locations/hotels/by-geocode",
      { latitude, longitude, radius, "page[limit]": pageLimit }
    );

    return NextResponse.json(response.data);
  } catch (err: any) {
    console.error("NEARBY HOTELS ERROR:", err);
    return NextResponse.json(
      { error: err?.description || err?.message || "Unknown error" },
      { status: 500 }
    );
  }
}
